.projects {
  min-height: 100vh;

  &__to-main {
    position: fixed;
    top: 15px;
    left: 15px;

    padding-right: 15px;

    font-size: rem(16);
    text-decoration: none;
    color: $first-color;

    border-right: 1px solid $second-color;
  }

  &__list {
    max-width: 1000px;
    margin-top: 50px;

    @media (min-width: 768px) {
      margin-top: 100px;
    }
  }

  &__item {
    margin-bottom: 15px;

    @media (min-width: 768px) {
      margin-bottom: 20px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__links {
    margin-top: 2px;
    font-size: rem(14);

    & > * {
      margin-right: 5px;
      margin-left: 5px;
    }
  }

  &__link {
    color: $first-color;
    text-decoration: none;
  }

  &__name {
    @include dotdotdot();
    font-weight: 600;
    font-size: rem(18);
  }

  &__separator {
    margin-left: 5px;
    margin-right: 5px;
  }

  &__description {
    font-size: rem(16);
  }
}
